.home {
  width:100%;
  height:100%;
  padding:0;
  margin:0;
  position:relative;
}
.select {
  width:100%;
  height:100%;
  a {
    width: 50%;
    height: 100%;
    float: left;
    display: block;
    position: relative;
    &:before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: url('/images/opdrachtgever.jpg') no-repeat center center;
      background-size: cover;
      transition: opacity 150ms;
    }
    :not(:first-child) {
      &:before {
        background:url('/images/opdrachtgever.jpg') no-repeat center center;
        background-size:cover;
      }
    }
  }
}